* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #6f81a1;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  transition: background-color .3s;
}

img {
  max-width: 100%;
  display: block;
}

.task-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding: 50px 0 0;
  list-style: none;
  display: flex;
}

.task-item {
  width: 250px;
  background-color: #842642;
  border: solid #3c0e0b;
  border-radius: 8px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 22px;
}

.link, .return-link {
  color: #d1db19;
  padding: 10px 15px;
  text-decoration: none;
  transition: color .2s, background-color .2s;
  display: block;
}

.link {
  height: 100%;
}

.return-link:hover, .link:hover {
  color: #19db87;
  background-color: #3c0e0b;
}

.return-link {
  width: 150px;
  background-color: #842642;
  border: solid #3c0e0b;
  border-radius: 4px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
}

.action-btns {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.action-btn {
  color: #fff;
  text-shadow: 0 -1px 4px #fff, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px red;
  cursor: pointer;
  background: #333;
  border-radius: 4px;
  padding: 15px;
  font-family: Courier New, Courier, monospace;
  font-weight: 700;
}

